import React, { useRef, useEffect, useState } from 'react';
import * as d3 from 'd3';
import { weightedVoronoi } from 'd3-weighted-voronoi';
import styles from './VoronoiChart.module.css';

const VoronoiChart = () => {
  
  const ref = useRef();
  const [currentYear, setCurrentYear] = useState(2020);

  

  useEffect(() => {
    const data = [
      { Generation: "Generation Z", Ages: "18-25", Values: [16043, 20803, 25851], x: null, y: null },
      { Generation: "Millennials", Ages: "26-41", Values: [87448, 100906, 115784], x: null, y: null },
      { Generation: "Generation X", Ages: "42-57", Values: [140643, 146164, 154658], x: null, y: null },
      { Generation: "Baby boomers", Ages: "58-76", Values: [97290, 95607, 96087], x: null, y: null },
      { Generation: "Silent Generation", Ages: "77+", Values: [41281, 39859, 39345], x: null, y: null }
    ];

    const width = 300;
    const height = 300;

    data.forEach(d => {
        d.x = Math.random() * (width - 50) + 25;
        d.y = Math.random() * (height - 50) + 25;
      });
      

    // Max debt calculation
    const maxDebt = d3.max(data, (d) => Math.max(...d.Values));

    // Create the drop-shadow filter
    const dropShadowFilter = d3.select(ref.current)
      .append("defs")
      .append("filter")
      .attr("id", "drop-shadow")
      .attr("width", "150%")
      .attr("height", "150%");

    dropShadowFilter.append("feGaussianBlur")
      .attr("in", "SourceAlpha")
      .attr("stdDeviation", 3);

    dropShadowFilter.append("feOffset")
      .attr("dx", 2)
      .attr("dy", 2)
      .attr("result", "offsetblur");

    const feMerge = dropShadowFilter.append("feMerge");

    feMerge.append("feMergeNode");
    feMerge.append("feMergeNode")
      .attr("in", "SourceGraphic");

    const svg = d3
      .select(ref.current)
      .attr('width', width)
      .attr('height', height)
      .style("filter", "url(#drop-shadow)");

    const g = svg.append('g');

    const color = d3.scaleOrdinal(d3.schemeCategory10);

    function updateVoronoi(yearIndex) {
        const polygons = weightedVoronoi()
          .weight((d) => d.Values[yearIndex] / maxDebt * 100)
          .clip([
            [0, 0],
            [width, 0],
            [width, height],
            [0, height],
          ])
          .size([width, height])(data);

          console.log(polygons)
    
        const paths = g.selectAll('path')
          .data(polygons);
    
        paths
          .enter()
          .append('path')
          .attr('d', (d) => `M${d.join('L')}Z`)
          .attr('fill', (d, i) => color(i))
          .attr('opacity', 0)
          .attr('stroke', 'white')
          .on('mouseover', function (event, d) {
            d3.select(this).attr('opacity', 1);
          })
          .on('mouseout', function (event, d) {
            d3.select(this).attr('opacity', 0.8);
          })
          .transition()
          .duration(500)
          .attr('opacity', 0.8);
    
        paths
          .exit()
          .transition()
          .duration(500)
          .attr('opacity', 0)
          .remove();
    
        paths
          .transition()
          .duration(500)
          .attr('d', (d) => `M${d.join('L')}Z`)
          .attr('fill', (d, i) => color(i))
          .attr('opacity', 0.8);
      }
    
 
  
    let currentYearIndex = 0;
    updateVoronoi(currentYearIndex);

    setInterval(() => {
        currentYearIndex = (currentYearIndex + 1) % 3;
        setCurrentYear(2020 + currentYearIndex);
        updateVoronoi(currentYearIndex);
      }, 3000);
      
  }, []);

  return (
    <div className={styles.voronoiContainer}>
      <h3>Year: {currentYear}</h3>
      <svg ref={ref} className={styles.voronoiSvg}></svg>
    </div>
  );
};

export default VoronoiChart;